/*************************************************+++++FOTOS */
  async takePicture(op) {
    const image = await Plugins.Camera.getPhoto({
      correctOrientation: true,
      height: 200,
      width: 200,
          quality: 60,
      allowEditing: true,
      resultType: CameraResultType.DataUrl,
      source: CameraSource.Camera
    });
    this.authenticationService.generateFromImage(image.dataUrl, 200, 200, 0.5, data => {
      switch (op) {
        case 'foto1a': this.foto1img = data; break;
        case 'foto2a': this.foto2img = data; break;
        case 'foto3a': this.foto3img = data; break;
          default:
      }
  });
  }
  uploadPWA2(op , controlx) {
    const fileList: FileList = controlx;
    if (fileList && fileList.length > 0) {
      this.firstFileToBase64(fileList[0]).then((result: string) => {
        this.authenticationService.generateFromImage(result, 200, 300, 0.5, data => {
          switch (op) {
            case 'foto1a':  this.foto1img = data; break;
            case 'foto2a':  this.foto2img = data; break;
            case 'foto3a':  this.foto3img = data;
       //     this.Form.controls['foto1'].patchValue({nombre: 'uno', tamaño : 838383});
             break;
              default:
          }
        });
      }, (err: any) => {
        switch (op) {
          case 'foto1a':  this.foto1img = null; break;
          case 'foto2a':  this.foto2img = null; break;
          case 'foto3a':  this.foto3img = null; break;
            default:
        }
      });
    }
  }
  uploadPWA(op) {
    switch (op) {
      case 'foto1a':  if (this.foto1a == null ) { return; }
      this.uploadPWA2(op , this.foto1a.nativeElement.files); break;
      case 'foto2a':  if (this.foto2a == null ) { return; }
      this.uploadPWA2(op , this.foto2a.nativeElement.files); break;
      case 'foto3a':  if (this.foto3a == null ) { return; }
      this.uploadPWA2(op , this.foto3a.nativeElement.files); break;
        default:
    }
  }
  private firstFileToBase64(fileImage: File): Promise<{}> {
    return new Promise((resolve, reject) => {
      const fileReader: FileReader = new FileReader();
      if (fileReader && fileImage != null) {
        fileReader.readAsDataURL(fileImage);
        fileReader.onload = () => {
          resolve(fileReader.result);
        };
        fileReader.onerror = (error) => {
          reject(error);
        };
      } else {
        reject(new Error('No file found'));
      }
    });
  }
  async takePicture2(op) {
    if ( op === 'foto1a')  {     this.foto1a.nativeElement.click();  }
    if ( op === 'foto2a')  {     this.foto2a.nativeElement.click();  }
    if ( op === 'foto3a')  {     this.foto3a.nativeElement.click();  }
  }
  async fotodatos( op ) {
    const actionSheet = await this.actionSheetController.create({
      header: 'Foto',
      cssClass: 'my-custom-class',
      buttons: [{
        text: 'Galeria',
        role: 'destructive',
        icon: 'image-outline',
        handler: () => {
          this.takePicture2(op);
        }
      }, {
        text: 'Camara',
        icon: 'camera-outline',
        handler: () => {
          this.takePicture(op);
        }
      }]
    });
    await actionSheet.present();
  }
