async combodatoslimpia(comp)
{
  this.Form.controls[comp].setValue('');
}
async combodatos( op ,  catalogot, frmt, comp, labelx) {
    const modal = await this.modalController.create({
      component: MyModalComboPage,
      swipeToClose: false,
      backdropDismiss: false,
     animated: true,
      presentingElement: await this.modalController.getTop(),
      componentProps: { variables:  this.variables, userinfo: this.userinfo
        ,  record2 : null , op: this.idllave + '-' + catalogot, frm: frmt, idllave : this.idllave, titlex: 'Buscar ' + labelx}
      // , cssClass: 'modal-fullscreen'
    });
    modal.onWillDismiss().then((data) => {
      let datax: any = [];
      datax = data;
        if (datax.data !==  'close')
        {
          if (datax.data) {this.Form.controls[comp].setValue(datax.data.nombre);
            //  this.Form.controls[comp].id = datax.data._id
            } else {
              this.Form.controls[comp].setValue('');
            }
        }

    
    });
    return await modal.present();
  }




